@import "../../global.scss";

section.modal{
    width: 100vw;
    height: 100vh;
    background: rgba(100, 100, 100, 0.7) ;
    position: fixed;
    text-align: center;
    z-index: 20;

div.modal-content{
    height: 250px;
    width: 300px;
    z-index: 21;
    background: whitesmoke;
    column-gap: 10px;
    aside:first-child{
        img{
            height: 120px;
            width: 150px;
            object-fit: cover
        };
    }
    aside:nth-child(2){
        flex: 1;
        button{
            background: blue;
            color: white;
            padding: 3px 9px;
            border: none;
            font-weight: bold;
            &:hover{
                transform: scale(1.1);
            }
        }
    }

}
}